import ErrorReport from '@components/ui/ErrorReport';
import { RichTextView } from '@components/ui/RichText/Viewer';
import { useChecklistLayoutData } from '@lib/hooks/useMatchesData';

export default function ChecklistSlideOutIndex() {
  const { item } = useChecklistLayoutData();
  return (
    <div className="col-span-2 my-3 mx-5 grid grid-rows-[auto_1fr] overflow-y-scroll min-h-[10rem]">
      <h4 className="font-bold text-xl mb-2">Description</h4>
      <RichTextView className="overflow-auto mt-3">
        { item.templateItem.description === ''
          ? item.templateItem.reference?.description ?? ''
          : item.templateItem.description }
      </RichTextView>
    </div>
  );
}

export const ErrorBoundary = ErrorReport;
